* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: none;
}

html {
  scroll-behavior: smooth;
}

html,
body,
main {
  width: 100%;
  height: 100%;
}

body {
  font-family: sans-serif;
  font-size: 16px;
}

button {
  position: relative;
  z-index: 1;
  overflow: hidden;
  cursor: pointer;
  width: 50%;
  margin: auto;
  padding: 10px;
  background: linear-gradient(
    to right,
    rgba(0, 128, 128, 0.688),
    rgba(255, 182, 193, 0.73)
  );
  border: none;
  border-radius: 3px;
  color: #000;
  line-height: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.9rem;
  -webkit-text-stroke: 1px teal;
  transition: all 0.3s linear;
}

button:hover {
  -webkit-text-stroke: 1px pink;
}

button:before {
  position: absolute;
  content: "";
  left: -100%;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to left,
    rgba(4, 4, 4, 0.688),
    rgba(255, 255, 255, 0.73)
  );
  border: none;
  border-radius: 3px;
  transition: all 0.3s linear;
  z-index: -1;
}

button:hover:before {
  left: 0;
}

/* MAIN */

main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
  height: 100vh;
  background: radial-gradient(
    rgba(255, 71, 157, 0.14) 50%,
    rgba(190, 246, 252, 0.752)
  );
}

body {
  width: 100%;
  margin: auto;
  background-color: #dbb9cd;
