.game--container {
  width: 85%;
  margin: auto;
  height: 80%;
  background-color: #222;
  background: url("../resources/images/stars.webp") no-repeat center
    center/cover;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 20px 10px #222222a9;
}

